ight: 24px;\n  width: 340px;\n  border: none;\n  &:focus {\n    outline: none;\n  }\n}\n\n.review--posting--button--forFlex {\n  display: flex;\n  justify-content: end;\n}\n\n.review--posting--button {\n  @include medium_kor;\n  color: $lightblue;\n  background: none;\n  padding: 0;\n  border: none;\n  padding: 5px;\n  border-radius: 10px;\n  &:hover {\n    cursor: pointer;\n  }\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_light: rgb(179, 188, 189);\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n"],"sourceRoot":""}]);
